import { useFrameworks, usePlatforms } from "../../data";
import Icon from "../../components/icon";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import Spinner from "../../components/spinner";
import { makeSearchUrl } from "./../search/page";

function PackageCompatibility(props) {
  const allFrameworks = useFrameworks().data;
  const allPlatforms = usePlatforms().data;
  if (!allFrameworks || !allPlatforms) return <Spinner />;
  const getTitle = (items, name) => {
    const item = items.find((item) => item.name === name);
    return item ? item.title : name;
  };
  const nameCmp = (a, b) => {
    if (a > b) return 1;
    if (a < b) return -1;
    return 0;
  };
  return (
    <>
      <h2 className="h3">Compatibility</h2>
      <div className="row">
        <div className="col">
          <table className="table table-hover">
            <thead>
              <tr>
                <th scope="col">Framework</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              {!(props.data.frameworks || []).length ? (
                <tr>
                  <td colSpan="2">No information</td>
                </tr>
              ) : props.data.frameworks.includes("*") ? (
                <tr>
                  <td className="w-100">
                    <Link
                      to={makeSearchUrl({
                        query: { keyword: "framework" },
                        type: "tool",
                      })}
                    >
                      Any supported framework
                    </Link>
                  </td>
                  <td className="text-success">
                    <Icon name="check-circle" size="20" />
                  </td>
                </tr>
              ) : (
                props.data.frameworks.sort(nameCmp).map((name) => (
                  <tr className="align-middle">
                    <td className="w-100">
                      <Link
                        to={makeSearchUrl({
                          query: name,
                          type: "tool",
                        })}
                      >
                        {getTitle(allFrameworks, name)}
                      </Link>
                    </td>
                    <td className="text-success">
                      <Icon name="check-circle" size="20" />
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
        <div className="col">
          <table className="table table-hover">
            <thead>
              <tr>
                <th scope="col">Platform</th>
                <th scope="col"></th>
              </tr>
            </thead>
            <tbody>
              {!(props.data.platforms || []).length ? (
                <tr>
                  <td colSpan="2">No information</td>
                </tr>
              ) : props.data.platforms.includes("*") ? (
                <tr>
                  <td className="w-100">
                    <Link
                      to={makeSearchUrl({
                        type: "platform",
                      })}
                    >
                      Any supported platform
                    </Link>
                  </td>
                  <td className="text-success">
                    <Icon name="check-circle" size="20" />
                  </td>
                </tr>
              ) : (
                props.data.platforms.sort(nameCmp).map((name) => (
                  <tr className="align-middle">
                    <td className="w-100">
                      <Link
                        to={makeSearchUrl({
                          query: name,
                          type: "platform",
                        })}
                      >
                        {getTitle(allPlatforms, name)}
                      </Link>
                    </td>
                    <td className="text-success">
                      <Icon name="check-circle" size="20" />
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}

PackageCompatibility.propTypes = {
  data: PropTypes.shape({
    frameworks: PropTypes.arrayOf(PropTypes.string),
    platforms: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
};

export default PackageCompatibility;
